<template>
  <div class="row" ref="main">
    <div class="input-div">
      <textarea
        class="editor"
        v-model="jsonStr"
        @keydown.enter="handleEnter"
      ></textarea>
      <div class="tip">请输入JSON串</div>
    </div>
    <div class="output-div">
      <json-view :data="json" :deep="5" theme="vs-code" />
    </div>
  </div>
</template>

<script>
import jsonView from "vue-json-views";
export default {
  name: "index",
  components: {
    jsonView,
  },
  data() {
    return {
      jsonStr: "",
      json: {},
    };
  },
  mounted() {
    this.$refs.main;
  },
  methods: {
    convert() {},
    handleEnter(e) {
      console.log(e);
      this.json = JSON.parse(this.jsonStr);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.row {
  display: flex;
}
.main {
  overflow: hidden;
}
.input-div {
  height: 100vh;
  width: 40vw;
  position: relative;
}
.editor {
  position: relative;
  height: 100vh;
  width: 40vw;
  border: none;
  padding: 25px;
  background-color: #ffffffdd;
  z-index: 100;
}
.tip {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  text-align: center;
  line-height: 100vh;
  font-size: 30px;
  font-weight: bold;
  z-index: 10;
}
.json-view-container {
  height: 100vh;
  width: 60vw;
}
.output-div {
  width: 60vw;
  height: 100vh;
}
</style>



