<template>
  <div class="position">
    <div class="row main">
      <div ref="aim" class="aim"></div>
      <div id="container" class="map"></div>
      <div class="tool-left">
        <div class="row" style="position: relative">
          <div>
            <div class="row">
              <label class="radio row" @click="select($event)">
                <input
                  type="radio"
                  name="type"
                  value="0"
                  checked
                  style="height: auto !important"
                />
                <span>地理名</span>
              </label>
              <div style="width: 20px"></div>
              <label class="radio row" @click="select($event)">
                <input
                  type="radio"
                  name="type"
                  value="1"
                  style="height: auto !important"
                />
                <span>经纬度</span>
              </label>
              <div style="width: 20px"></div>
              <label class="radio row" @click="select($event)">
                <input
                  type="radio"
                  name="type"
                  value="2"
                  style="height: auto !important"
                />
                <span>画线</span>
              </label>
            </div>

            <div class="row" style="height: 30px">
              <label class="row">
                <input class="position_in" type="text" v-model="position" />
              </label>
              <button class="btn-normal" @click="regeo()">定位</button>
            </div>
          </div>
        </div>
        <div class="row" style="height: 30px; margin-top: 10px">
          <button class="btn-red" style="" @click="clear()">清除</button>
        </div>
      </div>
      <div class="tool-right">
        <div class="row">
          <div>
            <label class="row">
              <div style="font-size: 14px">坐标获取结果</div>
            </label>
            <div class="row">
              <input
                class="position_out"
                type="text"
                disabled
                readonly
                v-model="longlat"
              />
            </div>
          </div>
        </div>
        <div style="height: 10px"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { elSwitch } from "element-ui";
import httpMap from "@/api/map.js";
const api = {
  search: "/geocode/geoRecommend",
};
export default {
  name: "index",
  components: {
    elSwitch,
  },
  data() {
    return {
      longlat: "117.120085, 36.652488",
      position: "",
      stringPosition: "",
      map: null,
      operation: "0", //0点地理查询  1线经纬度查询 2画线模式
      lines: [],
      paintListener: null,
      aim_init: ["calc(100% - 15px)", "calc(100% - 15px)"],
      target_icon: require("@/assets/target.png"),
    };
  },
  watch: {
    operation(cur, old) {
      if (cur === "2") {
        this.map.clearMap();
        this.map.setStatus({
          dragEnable: false,
        });
      } else {
        this.map.setStatus({
          dragEnable: true,
        });
      }
    },
  },
  mounted() {
    const that = this;
    const container = document.getElementById("container");
    this.map = new AMap.Map("container", {
      resizeEnable: true,
      center: [117.120085, 36.652488],
    });
    this.map.on("click", function (e) {
      that.longlat = e.lnglat.getLng() + "," + e.lnglat.getLat();
      if (that.operation === "0" || that.operation == "1") {
        that.lines = [];
        that.copyToClipboard(that.longlat);
        that.map.setCenter([e.lnglat.getLng(), e.lnglat.getLat()]);
        var marker = new AMap.Marker({
          position: new AMap.LngLat(e.lnglat.getLng(), e.lnglat.getLat()),
          icon: that.target_icon,
          offset: new AMap.Pixel(-32, -32),
        });
        that.map.clearMap();
        that.map.add(marker);
      }
    });

    let pixel_point = [0, 0];
    this.map.setDefaultCursor("crosshair");

    container.addEventListener("mousemove", function (event) {
      const x = event.pageX;
      const y = event.pageY;
      const len_x = this.clientWidth;
      const len_y = this.clientHeight;
      const aim = that.$refs.aim;
      if (x > 5 && y > 5 && len_x - x > 5 && len_y - y > 5) {
        aim.style.transform = `translate(
        calc(-50% + ${x + 1}px), calc(-50% + ${y - 1}px)
      )`;
      } else {
        aim.style.transform = `translate(0,0)`;
      }
    });

    container.addEventListener("pointerdown", function (e) {
      if (that.operation === "2") {
        if (that.paintListener) {
          AMap.event.removeListener(that.paintListener);
        }
        that.paintListener = AMap.event.addListener(
          that.map,
          "mousemove",
          that.getLine
        );
      }
    });
    container.addEventListener("pointerup", function (e) {
      if (that.paintListener) {
        AMap.event.removeListener(that.paintListener);
      }
      if (that.operation === "2" && that.lines.length > 0) {
        let linestr = "";
        for (let idx = 0; idx < that.lines.length; idx++) {
          const point = that.lines[idx];
          if (linestr) {
            linestr += "|" + point.join(",");
          } else {
            linestr += point.join(",");
          }
        }
        that.copyToClipboard(linestr);
        that.paintLine(that.lines);
        that.lines = [];
      }
    });

    this.map.plugin(["AMap.ToolBar"], function () {
      // that.map.addControl(new AMap.ToolBar());
    });
    container.removeChild(document.querySelector(".amap-copyright"));
  },
  methods: {
    regeo() {
      const that = this;
      if (that.operation == "1") {
        if (!this.position) return;
        const xy = this.position.split(",");
        this.map.setCenter(xy);
        this.map.setZoom(15);
        var marker = new AMap.Marker({
          position: new AMap.LngLat(xy[0], xy[1]),
          icon: that.target_icon,
          offset: new AMap.Pixel(-32, -32),
        });
        this.map.add(marker);
      } else if (that.operation == "0") {
        httpMap(
          {
            uri: api.search,
            method: "post",
            params: {
              keyword: that.position,
              districtCode: "370100",
            },
          },
          function (res) {
            if (res.data.rows.length > 0) {
              const region = res.data.rows[0];
              that.longlat = region.lng + "," + region.lat;
              that.copyToClipboard(that.longlat);
              that.map.setCenter([region.lng, region.lat]);
              that.map.setZoom(15);
              var marker = new AMap.Marker({
                position: new AMap.LngLat(xy[0], xy[1]),
                icon: that.target_icon,
                offset: new AMap.Pixel(-32, -32),
              });
              that.map.add(marker);
            }
          }
        );
      }
    },
    select(event) {
      if (event.target.nodeName == "INPUT") {
        this.operation = event.target.value;
      }
    },
    clear() {
      if (this.operation == "2") {
        this.lines = [];
        this.map.clearMap();
      } else {
        this.longlat = "117.120085, 36.652488";
        this.lines = [];
        this.map.setCenter([117.120085, 36.652488]);
        var marker = new AMap.Marker({
          position: new AMap.LngLat(117.120085, 36.652488),
          icon: this.target_icon,
          offset: new AMap.Pixel(-32, -32),
        });
        this.map.clearMap();
        this.map.add(marker);
      }
    },
    getLine(event) {
      const len = this.lines.length;
      const curPoint = [event.lnglat.lng, event.lnglat.lat];
      if (len > 0) {
        const prePoint = this.lines[len - 1];
        const distinct = this.calculate(prePoint, curPoint);
        const divison = 20 / this.map.getZoom();
        const factor = Math.pow(divison, 8) * 0.0001;
        if (distinct > factor) {
          this.lines.push(curPoint);
          this.paintLine([prePoint, curPoint]);
        }
      } else {
        this.lines.push(curPoint);
      }
    },
    calculate(point1, point2) {
      const x = point1[0] - point2[0];
      const y = point1[1] - point2[1];
      return Math.sqrt(x * x + y * y);
    },
    paintLine(params) {
      let points = [];
      for (let idx = 0; idx < params.length; idx++) {
        const point = new AMap.LngLat(params[idx][0], params[idx][1]);
        points.push(point);
      }
      const line = new AMap.Polyline({
        path: points,
      });
      this.map.add(line);
    },
    // 复制到剪贴板
    copyToClipboard(text) {
      // 兼容非安全域，非安全域下不可使用navigator.clipboard.writeText
      if (navigator.clipboard && window.isSecureContext) {
        navigator.clipboard
          .writeText(text)
          .then(() => {
            console.log({
              message: "已复制到剪贴板",
              type: "success",
              duration: 1500,
            });
          })
          .catch((error) => {
            console.error("复制失败：", error);
          });
      } else {
        const tempInput = document.createElement("input");
        tempInput.setAttribute("value", text);
        document.body.appendChild(tempInput);
        tempInput.select();
        document.execCommand("copy");
        document.body.removeChild(tempInput);
        console.log({
          message: "已复制到剪贴板",
          type: "success",
          duration: 1500,
        });
      }
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.data {
  --aim-x: 100px;
  --aim-y: 100px;
}
.main {
  height: calc(100vh);
  width: calc(100vw);
  position: relative;
  overflow: hidden;
}

.map {
  height: 100%;
  width: 100%;
}
/* .aim {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #00000050;
  z-index: 1;
  pointer-events: none;
  outline: 34px dotted #fff;
  outline-offset: 100px;
} */
.aim {
  height: 200%;
  width: 200%;
  position: absolute;
  z-index: 1;
  margin: 0;
  pointer-events: none;
  padding: 0;
  box-sizing: border-box;
}
.aim:after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  background: #000;
  top: 50%;
  right: 0px;
}
.aim:before {
  content: "";
  display: block;
  position: absolute;
  width: 2px;
  height: 100%;
  background: #000;
  top: 0px;
  right: 50%;
}

.tool-right {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 10px;
  background-color: #00000088;
  border-radius: 5px;
  color: #ffffff;
}

.tool-left {
  position: absolute;
  top: 10px;
  left: 10px;
  padding: 10px;
  background-color: #00000088;
  border-radius: 5px;
  color: #ffffff;
}

.position_in {
  font-size: 14px;
  color: #000;
  border-top-left-radius: none;
  border-bottom-left-radius: none;
}

.position_out {
  font-weight: bold;
  font-size: 16px;
  width: 200px;
  color: rgb(254, 0, 0);
  background-color: rgba(246, 246, 246, 0.75);
  opacity: 1;
}

.define-btn {
  background-color: #609dff;
  line-height: 1.8;
  font-size: 14px;
  color: white;
  width: 50px;
  text-align: center;
  border: none;
  margin-left: 5px;
  border-radius: 5px;
}

.define-btn:hover {
  background-color: #3280fc;
}

.row {
  display: flex;
}

#hide-div {
  position: absolute;
  left: -100vw;
  top: -100vh;
}

.input-card {
  width: 70px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.radio > span {
  font-size: 14px;
  color: aliceblue;
}

input[type="radio"] + span::before {
  content: " ";
  display: inline-block;
  vertical-align: middle;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: 1px solid #d8d8d8;
  margin-right: 0.213333rem;
  box-sizing: border-box;
}
input[type="radio"]:checked + span::before {
  background-color: red;
  background-clip: content-box;

  padding: 0.149333rem;
  box-sizing: border-box;
}
input[type="radio"] {
  position: absolute;
  clip: rect(0, 0, 0, 0);
}
</style>



