import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import position from '@/pages/map/position/index'
import jsonformat from '@/pages/json/formatter/index'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/position_map',
      name: 'position_map',
      component: position
    }
    ,
    {
      path: '/json_format',
      name: 'json_format',
      component: jsonformat
    }
  ]
})



