import axios from 'axios'


const mapKey = {
    earth: "a4c15d2e9d1a460135b55cef7695bf7946b69dfb3378eccc5ea2a89521cacb91",
    sky: "4f0372b6be1b8ee4fd1258a3781d26b2",
    god: "55fea88c628b4d46823439c7a0210084"
}
const earth = axios.create({
    baseURL: "https://tiles.geovisearth.com/geo/v1",
    // baseURL: "https://restapi.amap.com/v3/",
    timeout: 5000,
    headers: { "Authorization": `Bearer ${mapKey.earth}`, "Content-Type": "application/json" }
})

const selectSockt = (prefixObject, callback) => {
    switch (prefixObject.uri) {
        case "/geocode/geoRecommend":
            // prefixObject.uri = "/place/text"
            // prefixObject.params.key = mapKey.god
            earth[prefixObject.method](prefixObject.uri, prefixObject.params).then(ok => {
                callback(ok.data)
            })
    }
}

export default function (prefixObject, callback) {
    return selectSockt(prefixObject, callback);
}




