<template>
  <div class="hello">
    <ul>
      <li v-for="item in utils" @click="route(item.path)">
        {{ item.label }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'index',
  data() {
    return {
      utils: [
        {
          label: "高德定位",
          path: "/position_map",
          id: 1,
        }, {
          label: "JSON格式化",
          path: "/json_format",
          id: 2
        }
      ]
    }
  },
  mounted() {
  },
  methods: {
    route(item) {
      console.log(this)
      console.log(this.$router)
      this.$router.push({ path: item })
    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
li {
  color: mediumslateblue;
  font-size: 20px;
  line-height: 2;
}

li::marker {
  color: #ffffff00;
}
</style>



